}

.registerbtn {
  background-color: transparent;
  text-decoration: none;
  height: 5vh;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.25rem !important;
  padding: 9px 33px 9px 33px;
  border-radius: 35px;
  border: 1px;
  gap: 10px;
  border: 1px solid #4982f6;
  display: flex;
  margin: 2.5rem;
  font-weight: 700;
  font-size: 15px;
  color: #4982f6;
}

@media (max-width: 780px) {
  .registerbtn {
    height: fit-content;
    padding: 1vh 4vh;
    margin-top: 3vh;
    margin-bottom: 2vh;
  }
}
.registerbtn:hover {
  background-color: #4982f6;
  color: white;
  transition: ease-out;
  transition-duration: 300ms;
}
/* Customize scrollbar appearance (optional) */
.scrollablediv::-webkit-scrollbar {
  width: 10px;
}

.scrollablediv::-webkit-scrollbar-thumb {
  background-color: #4982f6;
  border-radius: 200px;
}

